import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ServersService } from '../../servers.service';

@Component({
  selector: 'app-premission',
  templateUrl: './premission.component.html',
  styleUrls: ['./premission.component.less']
})
export class PremissionComponent implements OnInit {
  @Output('add') addFtn = new EventEmitter<any>();
  @Output('read') readFtn = new EventEmitter<any>();
  @Output('update') updateFtn = new EventEmitter<any>();
  @Output('delete') deleteFtn = new EventEmitter<any>();
  @Output('save') saveFtn = new EventEmitter<any>();
  @Output('addMenu') addMenuFtn = new EventEmitter<any>();
  @Output('addPremission') addPreFtn = new EventEmitter<any>();
  @Input() place: any;//接收父组件的值
  @Input() hasRead:boolean;
  @Input() hasSave: boolean;
  constructor(public server: ServersService) { }

  ngOnInit() {
  }
  add(){
    this.addFtn.emit();
  }
  read() {
    this.readFtn.emit();
  }
  update() {
    this.updateFtn.emit();
  }
  delete() {
    this.deleteFtn.emit();
  }
  save() {
    this.saveFtn.emit();
  }
  //新增菜单
  addMenu() {
    this.addMenuFtn.emit();
  }
  //新增权限
  addPremission() {
    this.addPreFtn.emit();
  }
}
