import { Component, OnInit, Pipe, PipeTransform } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ServersService } from '../../servers.service';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../../environments/environment';
import { NzMessageService } from 'ng-zorro-antd';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  ValidationErrors,
  Validators
} from '@angular/forms';

@Component({
  selector: 'app-role',
  templateUrl: './role.component.html',
  styleUrls: ['./role.component.less']
})
export class RoleComponent implements OnInit {
  public api_list = '/api/RoleManage/GetRoleList'; // 页面数据列表api
  public api_add = '/api/RoleManage/CreateRole'; // 新增行api
  public api_update = '/api/RoleManage/EditRole'; // 更新行api
  public api_delete = '/api/RoleManage/DeleteRole?id='; // 删除行api

  //列表相关数据
  public displayData: any = []; //存储列表数据
  public isLoading = false; //用于加载效果
  public Page = 1;          //初始页码
  public Rows = 10;         //显示行数
  public total = 1;         //总条数
  public Sord = null;       //正反序
  public OrderBy = null;    //排序字段
  public loading = true;    //开启加载
  public Sidx = '0';
  public colData = null;

  //modal 参数
  public modelTitle = '新增'; //弹窗标题
  public validateForm: FormGroup; //表单
  private headers = new HttpHeaders({ 'Content-Type': 'application/json' }); //发送post请求头部
  public isVisible = false; //控制窗口是否显示
  public roleId = ''; //行id

  //接收参数
  public appList:any = [];
  public searchApp:any = null;
  public breadcrumb = '角色管理';


  //初始化构造函数
  constructor(public server: ServersService, private http: HttpClient, private fb: FormBuilder, private msg: NzMessageService) {
    this.validateForm = this.fb.group({
      roleName: [null, [Validators.required]],
      description: [null],
    });
  }

  ngOnInit() {
    //获取应用程序列表
    this.server.getAppList().subscribe((data) => {
      this.appList = data;
      this.searchApp = data[0];
      //获取页面列表
      this.getDataList();
    })
  }
  //获取列表数据
  getDataList(){
    let options = {
      api: this.api_list,
      params: {
        Sidx: '0',
        Page: this.Page,
        Rows: this.Rows,
        AppId: this.searchApp.id
      }
    }
    this.server.getRxjsData(options).subscribe((data) => {
      this.loading = false;
      this.total = data.total;
      this.colData = data.pageResult.data;
      this.displayData = [...this.colData];
    })
  }

  //应用程序下拉组件改变事件
  appChange(event){
    this.getDataList();
  }
  //搜索事件
  searchData(keyword?, reset: boolean = false): void {
    this.isLoading = true;
    if (reset) {
      this.Page = 1;
    }
    this.loading = true;
    this.getList(keyword, this.Page, this.Rows, this.Sidx).subscribe((data: any) => {
      this.loading = false;
      this.total = data.total;
      this.colData = data.pageResult.data;
      this.displayData = [...this.colData];
      this.isLoading = false;
    });
  }
  getList(keyword, pageIndex: number = 1, pageSize: number = 10, Sidx): Observable<{}> {
    let params: any = {}
    params.Name = keyword ? keyword : '';
    params.AppId = this.searchApp.id;
    if (this.Page) {
      params.Page = this.Page;
    }
    if (this.Rows) {
      params.Rows = this.Rows;
    }
    if (this.Sidx) {
      params.Sidx = this.Sidx;
    }
    if (this.Sord) {
      params.Sord = this.Sord;
    }
    let options = {
      api: this.api_list,
      params: params
    }
    return this.server.getRxjsData(options);
  }
  //打开弹框
  showModal(): void {
    this.isVisible = true;
    this.roleId = '';
    //表单初始化
    this.resetForm();
  }
  //弹框确定事件
  handleOk(): void {
    this.isVisible = false;
  }
  //弹框取消事件
  handleCancel(): void {
    this.isVisible = false;
  }
  resetForm(): void {
    this.validateForm.reset();
    for (const key in this.validateForm.controls) {
      this.validateForm.controls[key].markAsPristine();
      this.validateForm.controls[key].updateValueAndValidity();
    }
  }
  //编辑行
  editRow(item) {
    this.roleId = item.roleId;
    //初始化表单值
    let initformData = new Observable<any>((observer) => {
      this.validateForm.setValue({ roleName: item.roleName, description: item.description });
      observer.next();
    })
    initformData.subscribe(() => {
      this.isVisible = true;
      this.modelTitle = '编辑';
    })
  }
  //保存行
  submitForm(value): void {
    let paramsObj: any = value;
    if (this.roleId) {
      paramsObj.roleId = this.roleId;
    }
    paramsObj.appId = this.searchApp.id;
    let options = {
      api: this.roleId ? this.api_update : this.api_add,
      params: value,
    }
    let postData = this.server.postRxjsData(options);
    postData.subscribe(() => {
      this.isVisible = false;
      this.searchData();
    });

  }

  //删除行
  deleteRow(item): void {
    const options = {
      api: this.api_delete + item.roleId,
    }
    let postData = this.server.postRxjsData(options);
    postData.subscribe(() => {
      this.searchData();
    });
  }

}
