import { Component, OnInit, Pipe, PipeTransform, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { ServersService } from '../../servers.service';
import { Observable } from 'rxjs';
import { environment } from '../../../environments/environment';
import { NzMessageService, UploadFile } from 'ng-zorro-antd';
import {
  FormBuilder,
  FormGroup,
} from '@angular/forms';
import { element } from '@angular/core/src/render3/instructions';
export interface TreeNodeInterface {
  menuId: number;
  level: number;
  expand: boolean;
  address: string;
  children?: TreeNodeInterface[];
}
@Component({
  selector: 'app-permission',
  encapsulation: ViewEncapsulation.None,
  templateUrl: './permission.component.html',
  styleUrls: ['./permission.component.less']
})
export class PermissionComponent implements OnInit {
  mapOfExpandData: { [key: string]: boolean } = {};
  private headers = new HttpHeaders({ 'Content-Type': 'application/json' }); //发送post请求头部
  public roleId: any;//配置ID
  isLoading = false; //用于加载效果
  public api: any = '/api/RoleManage/GetRoleList'; //获取角色列表
  public api_premission: any = '/api/RoleAuthorization/GetPermissionTreeList';//获取授权列表
  public api_submit: any = '/api/RoleAuthorization/SaveRolePermission'; //保存角色授权

  public colData: any;
  public displayData: any;  //列表角色显示数据
  public projectData: any; //授权列表
  public premissionData: any;
  public breadcrumb = '授权管理';
  //表格排序字段
  public sortName = null;
  public sortValue = null;
  public listOfSearchName = [];
  public Page = 1;
  public Rows = 10;
  public total = 20;
  public Name = '';
  public Id = null;
  public Sidx = '0';
  public Sord = null;
  public OrderBy = null;
  public loading = true;
  public modalTitle: any = '新增角色';
  public listOfMapData: any;
  public radioValue: any;
  public selectedPremission: any = [];
  public isOkLoading: boolean = false;
  public status: any;
  isAllDisplayDataChecked = false;
  isIndeterminate = false;
  listOfAllData: any[] = [];
  mapOfCheckedId: { [key: string]: boolean } = {};
  //接收参数
  public appList: any = [];
  public searchApp: any = null;
  
  currentPageDataChange($event: Array<{ roleId: number, description: string; roleName: string }>): void {
    this.displayData = $event;
    this.refreshStatus();
  }
  refreshStatus(): void {
    this.isAllDisplayDataChecked = this.displayData.every(
      item => this.mapOfCheckedId[item.roleId]
    );
    this.isIndeterminate = this.displayData.some(item => this.mapOfCheckedId[item.roleId]) && !this.isAllDisplayDataChecked;
  }
  checkAll(value: boolean): void {
    this.displayData.forEach(
      item => this.mapOfCheckedId[item.roleId] = value
    );
    this.refreshStatus();
  }
  constructor(public server: ServersService, private http: HttpClient, private fb: FormBuilder, private msg: NzMessageService) {
  }
  ngOnInit(): void {
    //获取应用程序列表
    this.server.getAppList().subscribe((data) => {
      this.appList = data;
      this.searchApp = data[0];
      //获取页面列表
      this.getDataList();
    })
  }
  //获取角色列表
  getDataList() {
    this.isOkLoading = true;
    let options = {
      api: this.api,
      params: {
        Sidx: '0',
        Page: this.Page,
        Rows: this.Rows,
        AppId: this.searchApp.id
      }
    }
    this.server.getRxjsData(options).subscribe((data) => {
      this.loading = false;
      this.total = data.total;
      this.displayData = data.pageResult.data;
      this.radioValue = data.pageResult.data[0].roleId.toString();
      this.getPremissionList(this.radioValue);
    })
    this.isOkLoading = false;
  }
  refreshData(roleId) {
    this.getPremissionList(roleId);
  }
  //获取权限列表
  getPremissionList(roleId) {
    this.selectedPremission = [];
    let options = {
      api: this.api_premission,
      params: {
        roleId: roleId,
      }
    }
    this.server.getRxjsData(options).subscribe((data) => {
      //判断权限全选功能
      // this.findCheckAll(data, 1);
      this.listOfMapData = data.data;
      this.findCheckAll(this.listOfMapData, 1);
      this.listOfMapData.forEach(item => {
        this.mapOfExpandedData[item.menuId] = this.convertTreeToList(item);
      });


    })
  }
  //树形表处理方法
  mapOfExpandedData: { [menuId: string]: TreeNodeInterface[] } = {};

  collapse(array: TreeNodeInterface[], data: TreeNodeInterface, $event: boolean): void {
    if ($event === false) {
      if (data.children) {
        data.children.forEach(d => {
          const target = array.find(a => a.menuId === d.menuId)!;
          target.expand = false;
          this.collapse(array, target, false);
        });
      } else {
        return;
      }
    }
  }

  convertTreeToList(root: object): TreeNodeInterface[] {
    let count = 0;
    root['children'].forEach(element => {
      if (element.selected) {
        count++
      }
      root['selected'] = (root['children'].length == count && root['useYn']) ? true : false;

    });
    const stack: any[] = [];
    const array: any[] = [];
    const hashMap = {};
    stack.push({ ...root, level: 0, expand: false });

    while (stack.length !== 0) {
      const node = stack.pop();
      this.visitNode(node, hashMap, array);
      if (node.children) {
        for (let i = node.children.length - 1; i >= 0; i--) {
          stack.push({ ...node.children[i], level: node.level + 1, expand: false, parent: node });
        }
      }
    }

    return array;
  }

  visitNode(node: TreeNodeInterface, hashMap: { [menuId: string]: any }, array: TreeNodeInterface[]): void {
    if (!hashMap[node.menuId]) {
      hashMap[node.menuId] = true;
      array.push(node);
    }
  }
  //权限数组提取
  findCheckAll(list, type) {
    let count = 0;
    list.forEach(element => {
      if (element.children) {
        this.findCheckAll(element.children, type)
      } else if (element.permission) {
        this.checkPermission(element, type)
      }
    })

  }
  //权限列表全选判断
  checkPermission(item, event) {
    if (!event) {
      item['selected'] = false;
      this.mapOfExpandedData[item.parentMenuId][0]['selected'] = false;
    } else {
      let count = 0;
      item.permission.forEach(element => {
        if (element.useYn) {
          count++;
        }
      });
      item.selected = count == item.permission.length ? true : false;
      if (event === true) {
        let count2 = 0;
        this.mapOfExpandedData[item.parentMenuId].forEach((element2, index) => {
          if (index != 0 && element2['selected']) {
            count2++
          }
        })
        if (count2 == this.mapOfExpandedData[item.parentMenuId].length - 1) {
          this.mapOfExpandedData[item.parentMenuId][0]['selected'] = true;
        }
      }

    }
  }
  //查看页面权限修改
  changeReadPage(item, event) {
    if (!event) {
      item['selected'] = false;
    } else {
      let count = 0;
      item.children.forEach((element, index) => {
        if (element['selected']) {
          count++
        }
      })
      if (count == item.children.length) {
        item['selected'] = true;
      }
    }
  }

  //菜单全选修改事件
  menuCheckAll(menuId, list) {
    let count = 0;
    if (list) {
      list.forEach(element => {
        if (element.menuId == menuId) {
          element.children.forEach(item => {
            if (item.selected) {
              count++;
            }
          })
          element.selected = count == element.children.length ? true : false;
        }
      })
    }

  }
  //行全选事件
  updateAllChecked(item, event) {
    // 改变子集
    if (item.permission) {
      this.mapOfExpandedData[item.parent.menuId].forEach(element => {
        if (element.menuId == item.menuId) {
          element['permission'] = element['permission'].map(element2 => {
            return {
              ...element2,
              useYn: event
            };
          });
        }
      });
      this.mapOfExpandedData[item.parent.menuId][0].children.forEach(element => {
        if (element.menuId == item.menuId) {
          element['permission'] = element['permission'].map(element2 => {
            return {
              ...element2,
              useYn: event
            };
          });
        }
      });
    }
    //改变上级选中
    if (item.level > 0) {
      if (!event) {
        this.mapOfExpandedData[item.parent.menuId][0]['selected'] = false;
      } else {
        let count = 0;
        this.mapOfExpandedData[item.parent.menuId].forEach((element, index) => {
          if (index != 0 && element['selected']) {
            count++
          }
        })
        if (count == this.mapOfExpandedData[item.parent.menuId].length - 1 && this.mapOfExpandedData[item.parent.menuId][0]['useYn']) {
          this.mapOfExpandedData[item.parent.menuId][0]['selected'] = true;
        }

      }
    } else {
      this.mapOfExpandedData[item.menuId].forEach(ele => {
        ele['useYn'] = event;
        ele['selected'] = event;
        if (ele['permission']) {
          ele['permission'].forEach(per => {
            per['useYn'] = event;
          });
        }
      })
    }

  }
  //保存数据
  submitForm(): void {
    this.selectedPremission = [];
    let dataList = [];
    for (let i in this.mapOfExpandedData) {
      if (this.mapOfExpandedData[i][0]['useYn']) {
        this.selectedPremission.push(this.mapOfExpandedData[i][0]['permissionId']);
      }
      dataList.push(this.mapOfExpandedData[i][0])

    }
    this.getSelectedPremission(dataList);
    //提取数据
    let paramsObj: any = {
      roleId: this.radioValue,
      buttonIds: this.selectedPremission,
    }
    let options = {
      api: this.api_submit,
      params: paramsObj
    }
    //保存权限
    let postData = this.server.postRxjsData(options);
    postData.subscribe(() => {
      this.status = new Date();
    });

  }
  getSelectedPremission(list) {
    if (list) {
      list.forEach(element => {
        if (element.children) {
          this.getSelectedPremission(element.children);
        } else {
          element.permission.forEach(item => {
            if (item.useYn) {
              this.selectedPremission.push(item.permissionId);
            }
          });
        }
      })
    }
  }
  //清除选项
  changeAllCheck(status) {
    this.selectedPremission = status ? this.selectedPremission : [];
    let dataList = [];
    for (let i in this.mapOfExpandedData) {
      this.mapOfExpandedData[i].forEach(item => {
        dataList.push(item)
      })

    }
    this.clearPremission(dataList, status);
    this.projectData.forEach(element => {
      element.useYn = status;
    });
  }
  clearPremission(list, status) {
    if (list) {
      list.forEach(element => {
        element['selected'] = status;
        if (element.children) {
          element['useYn'] = status;
          element['selected'] = status;
          this.clearPremission(element.children, status);
        } else {
          element.permission.forEach(item => {
            item.useYn = status;
            item['selected'] = status;
          });
        }
      })
    }
  }
  //应用程序下拉组件改变事件
  appChange(event) {
    this.getDataList();
  }
}

