import { Component, OnInit, Pipe, PipeTransform } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ServersService } from '../../servers.service';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../../environments/environment';
import { NzMessageService } from 'ng-zorro-antd';
import { validateMobile } from '../../validators/mobile.validators';
import { NzFormatEmitEvent } from 'ng-zorro-antd';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  ValidationErrors,
  Validators
} from '@angular/forms';
const parents = [];
const childs = {};
@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.less']
})
export class UserComponent implements OnInit {
  public api_list = '/api/UserManage/GetUserList'; // 页面数据列表api
  public api_add = '/api/UserManage/CreateUser'; // 新增行api
  public api_update = '/api/UserManage/EditUser'; // 更新行api
  public api_delete = '/api/UserManage/DeleteUser?id='; // 删除行api
  public api_update_password = '/api/UserManage/ChangePassword'; //修改密码
  public api_reset_password = '/api/UserManage/ResetUserPassword';//重置密码
  public api_RolesWithAppTree = '/api/UserManage/GetRolesWithAppTree' //获取角色树形结构
  //列表相关数据
  public displayData: any = []; //存储列表数据
  public isLoading = false; //用于加载效果
  public Page = 1;          //初始页码
  public Rows = 10;         //显示行数
  public total = 1;         //总条数
  public Sord = null;       //正反序
  public OrderBy = null;    //排序字段
  public loading = true;    //开启加载
  public Sidx = '0';
  public colData = null;
  selectRole: string[] = [];
  nodes = [];
  //modal 参数
  public modelTitle = '新增'; //弹窗标题
  public validateForm: FormGroup; //表单
  public validateFormPassword: FormGroup; //修改密码表单
  private headers = new HttpHeaders({ 'Content-Type': 'application/json' }); //发送post请求头部
  public isVisible = false; //控制窗口是否显示
  public userId = ''; //行id

  //接收参数
  public appList: any = []; //应用程序列表
  public searchApp: any = null; //应用程序下拉组件
  public openItem:any = {}; //接收详情数据
  public visibleDetail = false; //控制显示详情
  
  public roles:any = [];
  public breadcrumb = '用户管理';
  public values: any[] = null;
  public isVisiblePassword = false;//修改密码弹框
  public detailSelectRole = [];


  //初始化构造函数
  constructor(public server: ServersService, private http: HttpClient, private fb: FormBuilder, private msg: NzMessageService) {
    this.validateForm = this.fb.group({
      sysUserName: [null, [Validators.required]],
      mobile: [null, [Validators.minLength(11), Validators.maxLength(11), validateMobile]],
      email: [null, [Validators.email]],
      position:[null],
      telephone:[null],
      roleIds:[null],
      password: [null, [this.addUserPassword]],
    });
    //重置密码表单
    this.validateFormPassword = this.fb.group({
      oldPassword: [null, [Validators.required, Validators.minLength(6), Validators.maxLength(20)]],
      newPassword: [null, [this.newValidator]],
      checkedNewPassword: [null, [this.confirmValidator]],
    })
  }
  //新增用户密码输入验证
  addUserPassword = (control: FormControl): { [s: string]: boolean } => {
    if (this.userId) {
      return {}
    }
    if (!control.value) {
      return { required: true };
    }
    else if (control.value.length < 6) {
      return { length: true };
    }
    else if (control.value !== this.validateForm.controls.password.value) {
      return { confirm: true };
    }
    return {};
  };
  //验证新密码与确认密码是否不一样
  confirmValidator = (control: FormControl): { [s: string]: boolean } => {
    if (!control.value) {
      return { required: true };
    }
    else if (control.value.length < 6) {
      return { length: true };
    }
    else if (control.value !== this.validateFormPassword.controls.newPassword.value) {
      return { confirm: true };
    }
    return {};
  };
  //验证新密码与旧密码是否不一样
  newValidator = (control: FormControl): { [s: string]: boolean } => {
    if (!control.value) {
      return { required: true };
    }
    else if (control.value.length < 6) {
      return { length: true };
    }
    else if (control.value == this.validateFormPassword.controls.oldPassword.value) {
      return { equal: true };
    }
    return {};
  };
  ngOnInit() {
    //获取应用程序列表
    this.server.getAppList().subscribe((data) => {
      this.appList = data;
      this.searchApp = data[0];
      //获取页面列表
      this.getDataList();
      //获取用户角色
      this.server.getRoleList(this.searchApp.id).subscribe((data) => {
        this.roles = data;
      })
      //设置角色下拉组件数据
      parents.length = 0;
      data.forEach(element => {
        var obj: any = {
          value: element.id,
          label: element.label,
          id: element.id
        }
        parents.push(obj)
      });
    })
    //获取角色树
    
    let options = {
      api: this.api_RolesWithAppTree,
    }
    this.server.getRxjsData(options).subscribe((data) => {
      data.data.forEach(element => {
        element.value = '1-' + element.value;
        element.key = '1-' + element.key
        this.nodes.push(element)
      });
    })
  }
  //获取列表数据
  getDataList() {
    let options = {
      api: this.api_list,
      params: {
        Sidx: '0',
        Page: this.Page,
        Rows: this.Rows,
        AppId: this.searchApp.id
      }
    }
    this.server.getRxjsData(options).subscribe((data) => {
      this.loading = false;
      this.total = data.total;
      this.colData = data.pageResult.data;
      this.displayData = [...this.colData];
    })
  }
  //应用程序下拉组件改变事件
  appChange(event) {
    this.getDataList();
  }
  //搜索事件
  searchData(keyword?, reset: boolean = false): void {
    this.isLoading = true;
    if (reset) {
      this.Page = 1;
    }
    this.loading = true;
    this.getList(keyword, this.Page, this.Rows, this.Sidx).subscribe((data: any) => {
      this.loading = false;
      this.total = data.total;
      this.colData = data.pageResult.data;
      this.displayData = [...this.colData];
      this.isLoading = false;
    });
  }
  getList(keyword, pageIndex: number = 1, pageSize: number = 10, Sidx): Observable<{}> {
    let params: any = {}
    params.Name = keyword ? keyword : '';
    params.AppId = this.searchApp.id;
    if (this.Page) {
      params.Page = this.Page;
    }
    if (this.Rows) {
      params.Rows = this.Rows;
    }
    if (this.Sidx) {
      params.Sidx = this.Sidx;
    }
    if (this.Sord) {
      params.Sord = this.Sord;
    }
    let options = {
      api: this.api_list,
      params: params
    }
    return this.server.getRxjsData(options);
  }
  //打开弹框
  showModal(): void {
    this.isVisible = true;
    this.userId = '';
    //表单初始化
    this.resetForm();
  }
  //弹框确定事件
  handleOk(): void {
    this.isVisible = false;
  }
  //弹框取消事件
  handleCancel(): void {
    this.isVisible = false;
  }
  resetForm(): void {
    this.validateForm.reset();
    for (const key in this.validateForm.controls) {
      this.validateForm.controls[key].markAsPristine();
      this.validateForm.controls[key].updateValueAndValidity();
    }
  }
  //编辑行
  editRow(item) {
    this.userId = item.userId;
    //初始化表单值
    let initformData = new Observable<any>((observer) => {
      this.validateForm.setValue({ sysUserName: item.sysUserName, email: item.email, mobile: item.mobile, position: item.position, telephone: item.telephone, roleIds: item.roleIds, password: null });
      observer.next();
    })
    initformData.subscribe(() => {
      this.isVisible = true;
      this.modelTitle = '编辑';
    })
  }
  //保存行
  submitForm(value): void {
    let paramsObj: any = value;
    paramsObj.roleIds = [];
    if (this.userId) {
      paramsObj.userId = this.userId;
    }
    paramsObj.appId = this.searchApp.id;
    //提取子role id
    this.selectRole.forEach((element, index) => {
      this.nodes.forEach(ele => {
        if (element == ele.key) {
          this.selectRole.splice(index, 1)
          for (let entry of ele.children) {
            this.selectRole.push(entry.key)
          }
        }
      })
    });
    paramsObj.roleIds = this.selectRole;
    let options = {
      api: this.userId ? this.api_update : this.api_add,
      params: paramsObj,
    }
    let postData = this.server.postRxjsData(options);
    postData.subscribe(() => {
      this.isVisible = false;
      this.searchData();
    });

  }

  //删除行
  deleteRow(item): void {
    const options = {
      api: this.api_delete + item.userId,
    }
    let postData = this.server.postRxjsData(options);
    postData.subscribe(() => {
      this.searchData();
    });
  }
  //查看行详情
  openDetail(item):void{
    this.detailSelectRole = [];
    this.openItem = item;
    //处理角色
    item.roleIds.forEach(element => {
      this.nodes.forEach((element2) => {
        element2.children.forEach(element3 => {
          if (element == element3.key){
            this.detailSelectRole.push(element2.title+'-'+element3.title)
          }
        });
      })
    });
    this.visibleDetail = true;
  }
  //关闭查看详情
  closeDetail() {
    this.visibleDetail = false;
  }
  //获取子列表
  getChild(parentId) {
    this.server.getRoleList(parentId).subscribe((data) => {
      var arr = [];
      data.forEach(element => {
        var obj: any = {
          value: element.id,
          label: element.label,
          isLeaf: true
        }
        arr.push(obj)
      });
      if (arr.length == 0) {
        arr.push(
          {
            value: null,
            label: '暂无数据',
            isLeaf: true,
            disabled: true,
          }
        )
      }

      childs[parentId] = arr;
    })

  }
  //数据处理
  loadData(node, index) {
    return new Promise((resolve) => {
      setTimeout(() => {
        if (index < 0) { // if index less than 0 it is root node
          node.children = parents;
        } else if (index === 0) {
          node.children = childs[node.value];
        }
        resolve();
      }, 500);
    });
  }
  //点击选项
  onChanges(values: any): void {
    if (values.index == 0) {
      this.getChild(values.option.id)
    }
  }
  //打开编辑密码窗口
  changePassword() {
    this.isVisiblePassword = true;
  }
  //保存修改密码
  submitFormPassword(value) {
    let options = {
      api: this.api_update_password,
      params: value
    }
    options.params.userId = this.userId;
    let postData = this.server.postRxjsData(options);
    postData.subscribe(() => {
      this.isVisiblePassword = false;
    });
  }
  //重置密码
  resetPassword() {
    let options = {
      api: this.api_reset_password,
      params: {
        id: this.userId
      }
    }
    let postData = this.server.postRxjsData(options);
    postData.subscribe(() => {
    });
  }
  //下拉树选中事件
  onChange($event: string[]): void {
    console.log($event);
  }
}
