import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { NzMessageService } from 'ng-zorro-antd';
import { ServersService } from '../../servers.service';
import { OAuthService, JwksValidationHandler} from 'angular-oauth2-oidc';
import { authConfig } from '../../auth.config';
@Component({
  selector: 'app-default',
  templateUrl: './default.component.html',
  styleUrls: ['./default.component.less']
})
export class DefaultComponent implements OnInit {
  constructor(public server: ServersService, public msg: NzMessageService, private router: Router, private routerActive: ActivatedRoute, private oauthService: OAuthService,) { 
    this.configureWithNewConfigApi();
  }
  private configureWithNewConfigApi() {
    this.oauthService.configure(authConfig);
    this.oauthService.tokenValidationHandler = new JwksValidationHandler();
    this.oauthService.loadDiscoveryDocumentAndTryLogin();
  }
  public api_list = '/api/MenuManage/GetMenuTreeList?appId=';//左侧菜单列表
  public systemName = this.server.getSystemName();
  color: string = '#1890ff'; //头像背景色
  isCollapsed = false; //收否收缩
  triggerTemplate: TemplateRef<void> | null = null;
  logoTemplate: TemplateRef<void> | null = null;
  @ViewChild('trigger') customTrigger: TemplateRef<void>;

  isAllDisplayDataChecked = false;
  isIndeterminate = false;
  listOfDisplayData: any[] = [];
  listOfAllData: any[] = [];
  mapOfCheckedId: { [key: string]: boolean } = {};
  //右侧消息弹框
  visible = false;
  public menuData:any = []; //接收菜单
  //接收参数
  public appList: any = [];
  public searchApp: any = null;
  public breadcrumbs = [];
  public msgNum = 0;

  login():void{
    this.oauthService.initImplicitFlow('/some-state;p1=1;p2=2');
  }
  //登出
  logout(): void {
    this.oauthService.logOut();
    // this.msg.info('退出成功!');
    // this.router.navigateByUrl('/login');
  }
  public get name() {
    let claims:any = this.oauthService.getIdentityClaims();
    if (!claims) return 'gust';
    return claims.aud;
  }
  //查看消息列表
  openMsg(): void{
    if (this.msgNum){
      this.visible = true;
    }else{
      this.msg.info('暂无消息！');
    }
  }
  close(){
    this.visible = false;
  }
  ngOnInit() {
    this.router.events
      .subscribe((event) => {
        console.log('NavigationEnd:', event);
        let root: ActivatedRoute = this.routerActive.root;
      });
    //获取应用程序列表
    this.server.getAppList().subscribe((data) => {
      this.appList = data;
      this.searchApp = data[0];
      //获取页面列表
      this.getMenuList();
    })
  }
  get requestAccessToken() {
    return this.oauthService.requestAccessToken;
  }
  //获取菜单数据
  getMenuList() {
    const option = {
      api: this.api_list + this.searchApp.id,
    }
    let rxjsData = this.server.getRxjsData(option);
    rxjsData.subscribe((data) => {
      this.menuData = data[0].children;
      if(!localStorage.getItem('menuId')){
        this.server.setPremission(this.menuData[0].menuId);
      }
    })
  }

}
