import { Component, OnInit, Pipe, PipeTransform } from '@angular/core';

import { ServersService } from '../../servers.service';
import { Observable } from 'rxjs';
import { environment } from '../../../environments/environment';

import {
  FormBuilder,
  FormControl,
  FormGroup,
  ValidationErrors,
  Validators
} from '@angular/forms';
export interface TreeNodeInterface {
  menuKey: number;
  levelNumber: number;
  expand: boolean;
  children?: TreeNodeInterface[];
}
@Component({
  selector: 'app-menu',
  templateUrl: './menu.component.html',
  styleUrls: ['./menu.component.less']
})

export class MenuComponent implements OnInit {
  validateForm: FormGroup; //注册菜单表单
  validateFormPermission: FormGroup; //注册权限表单
  public uploadUrl: any = environment.API + 'ProjectManage/UploadImgToServer';
  public menuId: any;//菜单
  public permissionId: any; //权限id
  public api: any = '/api/MenuManage/GetMenuTreeList'; //列表api
  public api_delete: any = '/api/MenuManage/DeleteMenu?id=';//删除行api
  public api_add: any = '/api/MenuManage/CreateMenu'; //新增行api
  public api_update: any = '/api/MenuManage/EditMenu';//更新行api
  public api_add_permission: any = '/api/MenuManage/CreatePermission';//新增权限
  public api_update_permission: any = '/api/MenuManage/EditPermission';//编辑权限
  public api_del_permission: any = '/api/MenuManage/DeletePermission?id=';//删除权限
  public colData: any;
  public listOfMapData: any;  //列表显示数据
  public visibleDetail: any = false; //查看详情
  public openItem: any = {};
  //表格排序字段
  public loading = true;
  public isOkLoading = false;
  public isVisible: boolean = false;
  public isVisiblePremission: boolean = false;
  public modalTitle: any = '新增菜单';
  public breadcrumb = '菜单管理';
  //接收参数
  public appList: any = [];
  public searchApp: any = null;
  constructor(private server: ServersService, private fb: FormBuilder) {
    this.validateForm = this.fb.group({
      menuName: [null, [Validators.required]],
      parentMenuId: [null],
      description: [null],
      iconString: [null],
      url: [null],
      menuOrder: [null]
    });
    this.validateFormPermission = this.fb.group({
      permissionName: [null, [Validators.required]],
      menuId: [null, [Validators.required]],
      permissionDec: [null],
    })
  }
  //树形表处理方法
  mapOfExpandedData: { [menuKey: string]: TreeNodeInterface[] } = {};

  collapse(array: TreeNodeInterface[], data: TreeNodeInterface, $event: boolean): void {
    if ($event === false) {
      if (data.children) {
        data.children.forEach(d => {
          const target = array.find(a => a.menuKey === d.menuKey)!;
          target.expand = false;
          this.collapse(array, target, false);
        });
      } else {
        return;
      }
    }
  }
  convertTreeToList(root: object): TreeNodeInterface[] {
    const stack: any[] = [];
    const array: any[] = [];
    const hashMap = {};
    stack.push({ ...root, levelNumber: 0, expand: false });

    while (stack.length !== 0) {
      const node = stack.pop();
      this.visitNode(node, hashMap, array);
      if (node.children) {
        for (let i = node.children.length - 1; i >= 0; i--) {
          stack.push({ ...node.children[i], levelNumber: node.levelNumber + 1, expand: false, parent: node });
        }
      }
    }

    return array;
  }
  visitNode(node: TreeNodeInterface, hashMap: { [menuId: string]: any }, array: TreeNodeInterface[]): void {
    if (!hashMap[node.menuKey]) {
      hashMap[node.menuKey] = true;
      array.push(node);
    }
  }

  ngOnInit(): void {
    //获取应用程序列表
    this.server.getAppList().subscribe((data) => {
      this.appList = data;
      this.searchApp = data[0];
      //获取页面列表
      this.getDataList();
    })
  }
  getDataList() {
    let options = {
      api: this.api,
      params: {
        appId: this.searchApp.id
      }
    }
    this.server.getRxjsData(options).subscribe((data) => {
      //数据处理
      data.forEach(element => {
        //级别+id
        element.key = element.level + '#' + element.menuId;//一级菜单
        element.menuKey = element.menuId;
        element.title = element.menuName;
        element.iconStr = element.icon;
        delete element.icon;
        if (element.children) {
          this.menuTransform(element.children)
        }
      });
      this.listOfMapData = data;
      this.listOfMapData.forEach(item => {
        this.mapOfExpandedData[item.menuId] = this.convertTreeToList(item);
      });
    })
  }
  //应用程序下拉组件改变事件
  appChange(event) {
    this.getDataList();
  }
  // 菜单列表格式化
  menuTransform(childrens) {
    childrens.forEach(element => {
      if (element.children) {
        element.key = element.level - 1 + '#' + element.menuId;
        element.menuKey = element.menuId
        element.title = element.menuName;
        element.iconStr = element.icon;
        delete element.icon;
        if (!element.children[0].level) {
          element.isLeaf = true;
        }
        this.menuTransform(element.children);
      } else {
        element.menuKey = '30' + element.permissionId;
        element.title = element.menuName || element.permissionName;
        return;
      }

    });
  }
  // tslint:disable-next-line:no-any
  compareFn = (o1: any, o2: any) => o1 && o2 ? o1.value === o2.value : o1 === o2;

  // 打开菜单表单
  showModal(): void {
    this.modalTitle = '新增菜单';
    this.menuId = '';
    this.isVisible = true;
    this.validateForm.reset();
    for (const menuId in this.validateForm.controls) {
      this.validateForm.controls[menuId].markAsPristine();
      this.validateForm.controls[menuId].updateValueAndValidity();
    }
  }
  // 打开权限表单
  showModalPremission(): void {
    this.modalTitle = '新增权限';
    this.permissionId = '';
    this.isVisiblePremission = true;
    this.validateFormPermission.reset();
    for (const menuId in this.validateFormPermission.controls) {
      this.validateFormPermission.controls[menuId].markAsPristine();
      this.validateFormPermission.controls[menuId].updateValueAndValidity();
    }
  }
  //重置菜单表单
  resetForm(): void {
    this.validateForm.reset();
    for (const key in this.validateForm.controls) {
      this.validateForm.controls[key].markAsPristine();
      this.validateForm.controls[key].updateValueAndValidity();
    }
  }
  //重置权限表单
  resetFormPermission(): void {
    this.validateFormPermission.reset();
    for (const key in this.validateForm.controls) {
      this.validateFormPermission.controls[key].markAsPristine();
      this.validateFormPermission.controls[key].updateValueAndValidity();
    }
  }

  //保存数据
  submitForm(value): void {
    this.isOkLoading = true;
    let paramsObj: any = {
      menuName: value.menuName,
      url: value.url,
      level: value.parentMenuId ? (parseInt(value.parentMenuId.split("#")[0]) + 1) : 1,
      parentMenuId: value.parentMenuId ? parseInt(value.parentMenuId.split("#")[1]) : 0,
      description: value.description,
      icon: value.iconString,
      menuOrder: value.menuOrder
    }
    if (this.menuId) {
      paramsObj.menuId = this.menuId;
    }
    let options = {
      api: this.menuId ? this.api_update : this.api_add,
      params: paramsObj
    }
    let postData = this.server.postRxjsData(options);
    postData.subscribe(() => {
      this.isVisible = false;
      this.isOkLoading = false;
      this.visibleDetail = false;
      this.getDataList();
    });

  }
  //保存数据
  submitFormPremission(value): void {
    this.isOkLoading = true;
    let paramsObj: any = {
      permissionName: value.permissionName,
      menuId: parseInt(value.menuId.split("#")[1]),
      description: value.permissionDec,
      menuOrder: value.menuOrder
    }
    if (this.permissionId) {
      paramsObj.permissionId = this.permissionId;
    }
    let options = {
      api: this.permissionId ? this.api_update_permission : this.api_add_permission,
      params: paramsObj
    }
    let postData = this.server.postRxjsData(options);
    postData.subscribe(() => {
      this.isVisiblePremission = false;
      this.isOkLoading = false;
    });


  }
  handleCancel(): void {
    this.isVisible = false;
    this.isVisiblePremission = false;
  }
  //删除记录
  deleteRow(value) {
    const options = {
      api: this.api_delete + value,
      params: value
    }
    let postData = this.server.postRxjsData(options);
    postData.subscribe(() => {
      this.getDataList();
    });

  }
  //删除权限
  delPermission(value) {
    const options = {
      api: this.api_del_permission + value,
      params: value
    }
    let postData = this.server.postRxjsData(options);
    postData.subscribe(() => {
      this.getDataList();
    });

  }
  //编辑菜单
  editformData(item) {
    this.modalTitle = '编辑菜单'
    // 配置弹框数据
    this.menuId = item.menuId;
    //表单赋值
    let initformData = new Observable<any>((observer) => {
      this.validateForm.setValue({ menuName: item.menuName, parentMenuId: item.parent ? item.parent.key : null, iconString: item.iconStr, url: item.url, description: item.description, menuOrder: item.menuOrder });
      observer.next();
    })
    initformData.subscribe(() => {
      this.isVisible = true;
    })
  }
  //编辑权限
  editePermission(item) {
    // 配置弹框数据
    this.permissionId = item.permissionId;
    this.validateFormPermission.setValue({ permissionName: item.permissionName, menuId: item.parent.key, permissionDec: item.description });
    this.isVisiblePremission = true;
  }
  //上级菜单树选择事件
  onChange($event: any): void {
    this.validateForm.value.level = $event.split("#")[0]
  }
  //查看详情
  openDetail(item) {
    this.openItem = item;
    this.visibleDetail = true;
  }
  closeDetail() {
    this.visibleDetail = false;
  }
}
