import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { environment } from '../../../environments/environment';
import { Router } from '@angular/router';
import { ServersService } from '../../servers.service';
import { OAuthService } from 'angular-oauth2-oidc';
import { authPasswordFlowConfig } from '../../auth-password-flow.config';
import { NzMessageService } from 'ng-zorro-antd';
import {
  AbstractControl,
  FormBuilder,
  FormGroup,
  Validators
} from '@angular/forms';
import {  } from '@angular/forms';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.less']
})
export class LoginComponent implements OnInit {
  private headers = new HttpHeaders({ 'Content-Type': 'application/json' }); //发送post请求头部
  public api = 'Account/Login';
  public systemName = this.server.getSystemName();
  validateForm: FormGroup;

  constructor(private fb: FormBuilder, public http: HttpClient, public router: Router, public server: ServersService, private oauthService: OAuthService, private msg: NzMessageService) {
    this.oauthService.configure(authPasswordFlowConfig);
    this.oauthService.loadDiscoveryDocument();
  }

  ngOnInit(): void {
    this.validateForm = this.fb.group({
      UserName: [null, [Validators.required]],
      Password: [null, [Validators.required]],
    });
  }

  submitForm(value): void {
    for (const i in this.validateForm.controls) {
      this.validateForm.controls[i].markAsDirty();
      this.validateForm.controls[i].updateValueAndValidity();
    }
    let options = {
      api: this.api,
      params: value
    }
    this.oauthService
      .fetchTokenUsingPasswordFlowAndLoadUserProfile(
        value.UserName,
        value.Password
      )
      .then(() => {
        this.msg.info('登录成功！');
        this.router.navigateByUrl('/home');
      })
      .catch(err => {
        this.msg.info('用户名或者密码错误！');
      });
    // this.router.navigate(['/home/']);
    // this.http.post(environment.projectUrl + options.api, JSON.stringify(options.params), { headers: this.headers }).subscribe((data: any) => {
    //   //  console.log(data)
    //   if (data.msg == '登录成功！') {
        
    //     //发送菜单数据到server
    //     // this.server.postMenuJson(data.menuJson);
    //   }
    // });
  }
}
