import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from '../environments/environment';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { NzMessageService } from 'ng-zorro-antd';
import { OAuthService, JwksValidationHandler } from 'angular-oauth2-oidc';

@Injectable({
  providedIn: 'root'
})
export class ServersService {
  public configUrl = environment.API; //获取环境文件中api地址
  private postHeaders = new HttpHeaders(
    { 'Content-Type': 'application/json', Authorization: "Bearer " + this.oauthService.getAccessToken() }
  )
  private getHeaders = new HttpHeaders(
    {Authorization: "Bearer " + this.oauthService.getAccessToken() }
  )
  public api_list_app = '/api/AppManage/GetAppList'; // 应用程序数据列表api
  public api_roles: any = '/api/RoleManage/GetRoleList'; //角色列表
  public api_premission: any = '/api/Common/GetButtonsPermission'; // 权限api
  public premissions: any = {}; //权限

  constructor(private http: HttpClient, private msg: NzMessageService, public oauthService:OAuthService) {
    if (localStorage.getItem('menuId')) {
      this.setPremission(localStorage.getItem('menuId'));
    }
   }
  getSystemName(){
    return "通用权限管理系统"
  }
  //通过rxjs获取数据
  getRxjsData(options) {
    var headers = new Headers({
      
    });
    return new Observable<any>((observer) => {
      this.http.get(this.configUrl + options.api, { params: options.params, headers: this.getHeaders  }).subscribe((response: any) => {
        if (response.status!==0) {
          observer.next(response);
        } else {
          window.location.reload();
          return false;
        }
      })
    })
  }
  postRxjsData(options) {
    return new Observable<any>((observer) => {
      this.http.post(environment.API + options.api, JSON.stringify(options.params), { headers: this.postHeaders }).subscribe(
        (response: any) => {
          this.msg.info(response.message);
          if (response.status) {
            observer.next();
          } else {
            return false;
          }
        },
        (error) => {
          if (error.status == 400 || error.status == 404) {
            this.msg.info(error.error)
          } else {
            this.msg.info('操作成功！');
            observer.next();
          }
        }
      );
    });

  }
  //获取应用程序列表
  getAppList() {
    //获取应用程序列表
    let options = {
      api: this.api_list_app,
      params: {
        Sidx: '0',
      }
    }
    return new Observable<any>((observer) => {
      this.getRxjsData(options).subscribe((data) => {
        //数据处理成可识别的下拉数据
        let array = [];
        data.pageResult.data.forEach(element => {
          let obj: any = {
            value: element.appId,
            label: element.appName,
            id: element.appId
          }
          array.push(obj)
        });
        observer.next(array);
      })
    })

  }
  //获取用户角色
  getRoleList(appid) {
    //获取应用程序列表
    let options = {
      api: this.api_roles,
      params: {
        Sidx: '0',
        AppId: appid
      }
    }
    return new Observable<any>((observer) => {
      this.getRxjsData(options).subscribe((data) => {
        //数据处理成可识别的下拉数据
        let array = [];
        data.pageResult.data.forEach(element => {
          let obj: any = {
            value: element.roleId,
            label: element.roleName,
            id: element.roleId
          }
          array.push(obj)
        });
        observer.next(array);
      })
    })
  }
  //通过promise获取数据
  getDataPromise(options) {
    return new Promise((resolve) => {
      this.http.get(this.configUrl + options.api).subscribe((response: any) => {
        resolve(response)
      })
    })
  }
  //设置用户权限
  //menuId:菜单id
  setPremission(menuId) {
    localStorage.setItem('menuId', menuId);
    const option = {
      api: this.api_premission,
      params: {
        menuId: menuId || 0,
      }
    }
    let rxjsData = this.getRxjsData(option);
    rxjsData.subscribe((data) => {
      if (data.data.length) {
        this.premissions = {};
        data.data.forEach(item => {
          if (item.permissionName == '查看页面') {
            this.premissions.hasReadPremission = true;
          }
          if (item.permissionName == '新增') {
            this.premissions.hasAddPremission = true;
          }
          if (item.permissionName == '编辑') {
            this.premissions.hasUpdatePremission = true;
          }
          if (item.permissionName == '删除') {
            this.premissions.hasDeletePremission = true;
          }
          if (item.permissionName == '导入') {
            this.premissions.hasInportPremission = true;
          }
          if (item.permissionName == '导出') {
            this.premissions.hasExportPremission = true;
          }
          if (item.permissionName == '新增数据类型') {
            this.premissions.hasAddDataTypePremission = true;
          }
          if (item.permissionName == '新增数据项目') {
            this.premissions.hasAddDataProjectPremission = true;
          }
          if (item.permissionName == '新增权限') {
            this.premissions.hasAddPrePremission = true;
          }
          if (item.permissionName == '新增菜单') {
            this.premissions.hasAddMenuPremission = true;
          }
          if (item.permissionName == '保存') {
            this.premissions.hasSavePremission = true;
          }

        });
      }
      return this.premissions;
    })
  }
}
