import { Component, OnInit } from '@angular/core';
import { OAuthService, JwksValidationHandler } from 'angular-oauth2-oidc';
import { } from 'angular-oauth2-oidc';
import { authConfig } from './auth.config';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { ActivatedRoute } from '@angular/router';
import { NzIconService } from 'ng-zorro-antd';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.less']
})
export class AppComponent implements OnInit {
  constructor(private authService: OAuthService, public router: Router, public routerActive: ActivatedRoute, private _iconService: NzIconService) {
    //设置iconfont
    this._iconService.fetchFromIconfont({
      scriptUrl: '//at.alicdn.com/t/font_1147472_93eqn4uhz5.js'
    });
    this.authService.configure(authConfig);
    this.authService.tokenValidationHandler = new JwksValidationHandler();
    this.authService.loadDiscoveryDocumentAndLogin();
  }
  ngOnInit() {
  }
}
