import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { ServersService } from '../../servers.service';
import { ActivatedRoute } from '@angular/router';
const parents = [];
const childs = {};
@Component({
  selector: 'app-role-cascader',
  templateUrl: './role-cascader.component.html',
  styleUrls: ['./role-cascader.component.less']
})
export class RoleCascaderComponent implements OnChanges {
  @Input() label: any;
  @Input() cascadarData: any; //接收父组件传递的参数
  @Output('sendData') sendData = new EventEmitter<any>();
  public values: any[] = null;

  constructor(public server: ServersService, public http: HttpClient, public router: ActivatedRoute) { }
  ngOnInit(): void {
    this.server.getAppList().subscribe((data)=>{
      parents.length = 0;
      data.forEach(element => {
        var obj: any = {
          value: element.id,
          label: element.label,
          id: element.id
        }
        parents.push(obj)
      });
    })
  }
  ngOnChanges(changes: SimpleChanges) {
    if (changes['cascadarData'] && changes['cascadarData'].currentValue) {
      this.values = this.cascadarData;
    }
  }
  //获取子列表
  getChild(parentId) {
    this.server.getRoleList(parentId).subscribe((data) => {
      var arr = [];
      data.forEach(element => {
        var obj: any = {
          value: element.id,
          label: element.label,
          isLeaf: true
        }
        arr.push(obj)
      });
      if (arr.length == 0) {
        arr.push(
          {
            value: null,
            label: '暂无数据',
            isLeaf: true,
            disabled: true,
          }
        )
      }

      childs[parentId] = arr;
    })

  }
  //数据处理
  loadData(node, index) {
    return new Promise((resolve) => {
      setTimeout(() => {
        if (index < 0) { // if index less than 0 it is root node
          node.children = parents;
        } else if (index === 0) {
          node.children = childs[node.value];
        }
        resolve();
      }, 500);
    });
  }
  //点击选项
  onChanges(values: any): void {
    if (values.index == 0) {
      this.getChild(values.option.id)
    } else {
      this.sendData.emit(values.option);
    }
  }
}